/// gEditorial Partials: Drafts Front ALL ----------------------------------------

.geditorial-wrap.drafts { // #editorial-drafts

	background: #444;
	border-top: 1px solid #999;
	border-bottom: 5px solid #666;
	box-shadow: 0px 2px 4px rgba(0,0,0,0.8), inset 0px 0px 4px rgba(0,0,0,0.5);
	color: #fff;
	display: none;
	height: 94%; //400px;

	left: 0;
	right: 0;

	// max-height: 400px;
	overflow: auto;
	padding: 15px 0;
	position: absolute;
	top: 28px;
	width: 100%;
	z-index: 100;

	&.-loading {
		// background: #444 url('images/wpspin_dark.gif') no-repeat center center;
	}

	.-content {
		visibility: hidden;

		a,
		a:visited {
			color: #fff;
		}

		.-empty {

		}

		.-block {

			min-width: 320px;

			border-left: 1px solid #777;
			float: left;
			margin: 0 0 0 15px;
			padding-top: 15px;
			padding-left: 15px;

			@include body-rtl() {
				border-left: none;
				border-right: 1px solid #777;
				float: right;
				margin: 0 15px 0 0;
				padding-left: 0;
				padding-right: 15px;
			}

			h3 {
				margin: 0 0 0.5em 0;
				padding: 0;
				font-size: 1.1em;
			}

			ul {
				font-size: 12px;
				line-height: 140%;
				list-style: none;
				margin: 0;
				padding: 0;

				li {
					margin: 0 0 6px;
				}
			}
		}
	}
}
